      "country": "Россия",
      "year": "2012",
      "duration": 46,
      "description": "Фильм-концерт группы «Ленинград», где героями являются не музыканты, а зрители. Целое этнографическое исследование на тему того, кто же такие поклонники «Ленинграда», выполненное против всех законов жанра. Сами музыканты выступают тут соучастниками съемочного процесса — одна из камер, например, закреплена у Шнурова на животе. Другие же камеры следуют за поклонниками группы, довольно смешно и точно фиксируя их эмоции и поступки от объятий до драк и передавая атмосферу оголтелого веселья и зашкаливающего адреналина, которыми славится «Ленинград». Ужасно смешно, до слез мило, временами страшно — эффект присутствия в полный рост.",
