      "country": "США",
      "year": "2013",
      "duration": 75,
      "description": "Его старший брат — рок-звезда, вокалист группы The National. Он — инфантильный тюфяк, не способный ничего доделать до конца и предпочитающий Judas Priest. Пока Мэтт Бернингер разъезжает по миру, Том Бернингер лежит на диване и рисует странные картинки — но вдруг у младшего появляется возможность стать режиссером документального фильма. «Mistaken for Strangers», снятый братом-неудачником про брата-знаменитость, даже не столько грандиозное кино о The National, сколько типическое исследование братских отношений: про дружбу, зависть, комплексы и любовь. Когда неотступно следующего за группой Тома увольняют, режиссер окончательно начинает снимать про себя — но фильм от этого получается только точнее и смешнее.",
