      "country": "США",
      "year": "2013",
      "duration": 81,
      "description": "Она была подругой Кобейна и, по легенде, придумала название одной великой песни, написав на стене «Курт пахнет Teen Spirit». Это, впрочем, далеко не главный факт в биографии Кэтлин Ханны — лидера феминистского движения движения riot grrrl, повлиявшего на группы в диапазоне от Sleater-Kinney до Pussy Riot. Ее почитают Ким Гордон и Кэрри Браунштейн, она всегда будет ролевой моделью для женщин, восстающих против неравноправия, — потому что умеет оставаться стойкой и знает, что делать. После панк-рок группы Bikini Kill Ханна основала электроклэш-трио Le Tigre, а затем внезапно пропала — об этом, а также о ее отношениях с Адамом Хоровитцем из Beastie Boys, редком характере и абсолютной честности фильм «The Punk Singer».",
