      "description": "Московский техно-дуэт Interchain предлагает воспринимать недостроенные здания как скульптуры, заостряя внимание на пересечении исторического и эстетического в замороженных стройках. Для погружения в тему, Евгений Горбунов и Андрей Ли исполняют импровизацию в одном из заброшенных строений, создавая собственную незавершенную аудиоконструкцию.",
