import React, { Suspense, useState } from 'react';
import { Route, Routes } from 'react-router-dom';
import './App.css';

import Preloader from '../Preloader/Preloader';
import PopupMenu from '../PopupMenu/PopupMenu';
import SavedMovies from '../SavedMovies/SavedMovies';
import SettingsProfile from '../SettingsProfile/SettingsProfile';

const Main = React.lazy(() => import('../Main/Main'));
const Movies = React.lazy(() => import('../Movies/Movies'));
const Login = React.lazy(() => import('../Login/Login'));
const Register = React.lazy(() => import('../Register/Register'));

function App() {
  const [openPopupMenu, setOpenPopupMenu] = useState(false);

  function handleClosePopupMenu() {
    setOpenPopupMenu(false);
  };

  function handleOpenPopupMenu() {
    setOpenPopupMenu(true);
  };

  return (
    <div className="app">
       <Suspense fallback={<Preloader />}>
        <Routes>
            <Route path='/movies-explorer-frontend/' element={ <Main /> }/>
            <Route path='/movies-explorer-frontend/signin' element={ <Login /> }/>
            <Route path='/movies-explorer-frontend/signup' element={ <Register /> }/>
            <Route path='/movies-explorer-frontend/movies' element={ <Movies handleOpenPopupMenu={ handleOpenPopupMenu }/> } />
            <Route path='/movies-explorer-frontend/saved-movies' element={ <SavedMovies handleOpenPopupMenu={ handleOpenPopupMenu } /> } />
            <Route path='/movies-explorer-frontend/profile' element={ <SettingsProfile handleOpenPopupMenu={ handleOpenPopupMenu } /> } />
        </Routes>
      </Suspense>

      <PopupMenu state={ openPopupMenu } closePopup={ handleClosePopupMenu } />
    </div>
  );
}

export default App;
